.loader-default {
    display: none;
    z-index: 9999;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba(255, 255, 255, 0.5)
}

.loader-default:after {
    content: '';
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 40px;
    height: 40px;
    position: absolute;
    left: calc((100vw / 2) - 20px);
    top: calc((100vh / 2) - 20px);
    border-radius: 50%;
    border: 7px solid #f57c00;
    border-bottom-color: transparent;
    -webkit-animation: spin--loader-default 0.8s linear infinite; /* Safari */
    animation: spin--loader-default 0.8s linear infinite;
}

.loader-aside-contents {
    display: none;
    z-index: 9999;
    position: fixed;
    width: 300px;
    left: 0;
    top: 270px;
    bottom: 0;
    background: rgba(255, 255, 255, 0.5)
}

.loader-aside-contents:after {
    content: '';
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 40px;
    height: 40px;
    position: absolute;
    left: calc((300px / 2) - 20px);
    top: calc((270px / 2) - 20px);
    border-radius: 50%;
    border: 7px solid #f57c00;
    border-bottom-color: transparent;
    -webkit-animation: spin--loader-default 0.8s linear infinite; /* Safari */
    animation: spin--loader-default 0.8s linear infinite;
}

.loader-ico {
    display: none;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 24px;
    height: 24px;
    position: absolute;
    left: calc((100vw / 2) - 20px);
    bottom: 12px;
}

.loader-ico:after {
    content: '';
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 24px;
    height: 24px;
    position: absolute;
    border-radius: 50%;
    border: 5px solid #f57c00;
    border-bottom-color: transparent;
    -webkit-animation: spin--loader-default 0.8s linear infinite;
    animation: spin--loader-default 0.8s linear infinite;
}

/* Safari */
@-webkit-keyframes spin--loader-default {
    0% {
        -webkit-transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
    }
}

@keyframes spin--loader-default {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@media (max-width: 720px) {
